import * as React from 'react';
import './userContent.less';

interface IUserContentProps {
    id:string
    username:string
    type:number
    nickname:string
    icon:string
    phone:string
    regtime:string
    lastlogintime:string
    isauthent:number
    ct:string
    mt:string
}

export class UserContent extends React.Component<IUserContentProps> {
  public render() {
    return (
      <div className="userContent">
        <p>
            {this.props.username}
        </p>
        <p>
            {this.props.type === 1?"":""}
        </p>
        <p>
            {this.props.nickname}
        </p>
        <p>
            <img src={this.props.icon}/>
        </p>
        <p>
            {this.props.phone}
        </p>
        <p>
            {this.props.regtime}
        </p>
        <p>
            {this.props.lastlogintime}
        </p>
        <p>
            {this.props.isauthent === 1?"":""}
        </p>
        <p>
            {this.props.ct}
        </p>
        <p>
            {this.props.mt}
        </p>
      </div>
    );
  }
}
