import axios from "../../util/Axios";

/**
 * 
 * @param page page=1
 * @description 
 */
export const apiGetPetAuditList = async (page:number,size:number,status:string[])=>{
    return await axios({
        method:"post",
        url: `/audit/petList`,
        data:{
            "page":page,
            "size":size,
            "status":status
        }
    })
}

