/**
 * 
 * @param BusinessArea   
 * @param BusinessRound 
 */
export const apiPublishBusinessArea = (BusinessArea:string[],BusinessRound:string)=>{
    return axios({
        method:"post",
        url:`/round/add`,
        data:{
            area:BusinessArea[BusinessArea.length - 1],
            round:BusinessRound
        }
    })
}
