/**
 * 
 * @param page 
 * @param size 
 */
export const apiPostAdvertisement = (
        type:number,
        relateType:number,
        relateId:string,
        url:string,
        title:string,
        content:string,
        image:string,
        cityCode:string
    )=>{
    return axios({
        method:"post",
        url: `/advertisement/add`,
        data:{
            "type":type,
            "relateType":relateType,
            "relateId":relateId,
            "url":url,
            "title":title,
            "content":content,
            "image":image,
            "cityCode":cityCode
        }
    })
}

/**
 * 
 * @param page 
 * @param size 
 */
export const apiEditAdvertisement = (
    id:number,
    type:number,
    relateType:number,
    relateId:string,
    url:string,
    title:string,
    content:string,
    image:string,
    cityCode:string
)=>{
return axios({
    method:"post",
    url: `/advertisement/edit`,
    data:{
        "id":id,
        "type":type,
        "relateType":relateType,
        "relateId":relateId,
        "url":url,
        "title":title,
        "content":content,
        "image":image,
        "cityCode":cityCode
    }
})
}

